import { mapMutations, mapGetters, mapActions } from "vuex";
import Loading from "@/components/Loading";

export const GoodsMixin = {
  components: { Loading },
  data() {
    return {
      loadingStatus: true // 
    };
  },
  computed: {
    ...mapGetters([
      "categoryList", // 
      "locationCity", // 
      "searchHistoryList", // 
      "browseHistoryList", // 
      "userToken", //  token
      "orderPaymentList", // 
      "addressInfo", // 
      "tempAddress" // 
    ])
  },
  methods: {
    ...mapMutations({
      setOrderPaymentList: "ORDER_PAYMENT_LIST", // 
      setTempAddress: "TEMP_ADDRESS" // 
    }),
    ...mapActions([
      "setCategoryList", // 
      "setLocationCity", // 
      "setSearchHistory", // 
      "deleteSearchHistory", // 
      "setBrowseHistory", // 
      "deleteOneBrowseHistory", // 
      "setUserToken", //  token
      "deleteUserToken", //  token
      "setAddressInfo", // 
      "deleteAddressInfo" // 
    ]),
    // 
    goGoodsDetails(item) {
      this.$router.push({
        name: "GoodsDetails",
        query: { goodsId: item.goodsId || item.id }
      });
    },
    // 
    back() {
      this.$router.go(-1);
    }
  }
};
