<template>
  <!--  -->
  <div class="scroll-wrapper" ref="scrollRef">
    <slot></slot>
  </div>
</template>

<script>
import BScroll from "better-scroll";

export default {
  name: "Scroll",
  props: {
    probeType: { type: Number, default: 1 }, //  scroll  | 1
    click: { type: Boolean, default: true }, //  click 
    listenScroll: { type: Boolean, default: false }, // 
    pullup: { type: Boolean, default: false }, // 
    pullDownRefresh: { type: Boolean, default: false }, // 
    beforeScroll: { type: Boolean, default: false }, // 
    bounce: { type: Object, default: () => ({ top: true }) }, // 
    swipeTime: { type: Number, default: 1600 }, //  ms
    timer: { type: Number, default: 20 },
    data: { type: Array, default: null }
  },
  watch: {
    data() {
      setTimeout(() => {
        //  data 
        this.refresh();
      }, this.timer);
    }
  },
  mounted() {
    //  Dom 
    setTimeout(() => {
      this._initScroll();
    }, 20);
  },
  methods: {
    _initScroll() {
      if (!this.$refs.scrollRef) return;

      //  better-scroll
      this.scroll = new BScroll(this.$refs.scrollRef, {
        probeType: this.probeType,
        click: this.click,
        pullDownRefresh: this.pullDownRefresh,
        bounce: this.bounce,
        swipeTime: this.swipeTime
      });

      //  |  better-scroll 
      if (this.listenScroll) {
        let _this = this; // pos  {x, y}
        this.scroll.on("scroll", pos => {
          // 
          _this.$emit("scroll", pos);
        });
      }

      //  |  better-scroll 
      if (this.pullup) {
        let _this = this;
        this.scroll.on("scrollEnd", () => {
          if (this.scroll.y <= this.scroll.maxScrollY + 50) {
            // 
            _this.$emit("scrollEnd");
          }
        });
      }

      //  |  better-scroll 
      if (this.pullDownRefresh) {
        let _this = this;
        this.scroll.on("scroll", pos => {
          if (pos.y > 50) {
            _this.$emit("scrollChange");
          }
        });
        // 
        this.scroll.on("pullingDown", () => {
          _this.$emit("onPullDown");
        });
      }

      //  better-scroll 
      if (this.beforeScroll) {
        let _this = this;
        this.scroll.on("beforeScrollStart", () => {
          _this.$emit("beforeScroll");
        });
      }
    },
    // 
    enable() {
      this.scroll && this.scroll.enable();
    },
    // 
    disable() {
      this.scroll && this.scroll.disable();
    },
    //  better-scroll 
    finishPullDown() {
      this.scroll && this.scroll.finishPullDown();
    },
    //  better-scroll DOM 
    refresh() {
      this.scroll && this.scroll.refresh();
    },
    // 
    scrollTo() {
      this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments);
    },
    //  
    scrollToElement() {
      this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments);
    }
  }
};
</script>

<style lang="scss" scoped></style>
